section.intro {
  width: 100%;
  .intro-content {
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-around;
    padding-top: 60px;
    .text-left {
      h2 {
        font-size: 3.5rem;
        color: $primary;
        margin-bottom: 15px; }
      h4 {
        color: $primary;
        font-weight: 500;
        margin-bottom: 30px; }
      .email-input {
        height: 50px;
        position: relative;
        display: flex;
        justify-content: space-between;
        input {
          border: none;
          border-radius: 25px;
          width: 100%;
          height: 100%;
          background-color: $input-bg;
          padding: 0;
          padding-left: 15px;
          font-size: 1rem;
          &:focus {
            outline: none; } }
        a.join-btn {
          position: absolute;
          top: 0;
          right: 0;
          width: 12%;
          background-color: $primary;
          color: #fff;
          padding: 10px 25px;
          border-radius: 25px;
          text-align: center;
          height: 30px; } } }
    .image-right {
      img {
        height: 600px; } } } }
